.snackbar {
  position: fixed;
  max-width: 500px;
  min-width: 300px;
  left: 5rem;
  top: 90vh;
  animation: 0.4s ease-out forwards slide-up;
}

@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}